[class*='player'] {
  background-repeat: no-repeat;
  background-size: 40%;
  background-position: center; }

[class*="icon"] {
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain; }

.icon-x, .player1 {
  background-image: url("./x.svg"); }

.icon-circle, .player2 {
  background-image: url("./circle.svg"); }

* {
  box-sizing: border-box; }

body {
  background-color: #2c3e50;
  color: #fff;
  font-family: sans-serif;
  margin: 0;
  padding: 0; }

body, button, .button {
  font-size: 1.2em; }

button, .button {
  -webkit-transition: background-color 180ms ease-in-out;
  -ms-transition: background-color 180ms ease-in-out;
  transition: background-color 180ms ease-in-out;
  background-color: #27ae60;
  border-width: 0px;
  border-radius: 5px;
  color: #fff;
  font-weight: 200;
  margin-top: 25px;
  padding: 10px 20px;
  text-decoration: none; }
  button:disabled, .button:disabled {
    background-color: #e74c3c;
    color: #ab1000; }

@media only screen and (min-device-width: 768px) {
  body, button, .button {
    font-size: 1em; } }

a {
  color: #27ae60; }

.new-game-button {
  display: block;
  width: 180px;
  text-align: center; }

label {
  display: block; }

input {
  -webkit-transition: background-color 180ms ease-in-out;
  -ms-transition: background-color 180ms ease-in-out;
  transition: background-color 180ms ease-in-out;
  background: #34495e;
  border: 1px solid #43586d;
  border-radius: 5px;
  color: #fff;
  font-weight: 200;
  padding: 10px; }
  input:focus {
    background: #667b90; }

form {
  font-weight: 200; }

.tic-tac-toe-app {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
  flex-direction: column; }

.app-footer {
  height: 70px;
  display: block; }

.done .board-slot:not(.winner) {
  opacity: .1; }
