.board {
  display: flex;
  flex-wrap: wrap;
  height: 200px;
  width: 200px;
  outline: 1px solid rgba(255, 255, 255, 0.1); }
  .board .filled {
    -webkit-transition: background-color 110ms linear;
    -ms-transition: background-color 110ms linear;
    transition: background-color 110ms linear; }
  .board .filled.winner {
    animation: win infinite alternate ease-in-out .4s; }

.board-slot {
  background-color: #34495e;
  border: 1px solid rgba(255, 255, 255, 0.1);
  cursor: pointer;
  height: 66.66667px;
  width: 66.66667px; }
  .board-slot:hover:not(.filled) {
    background-color: #3e5368;
    -webkit-transition: background-color 150ms linear;
    -ms-transition: background-color 150ms linear;
    transition: background-color 150ms linear; }

@media only screen and (min-device-width: 768px) {
  .board {
    display: flex;
    flex-wrap: wrap;
    height: 500px;
    width: 500px;
    outline: 1px solid rgba(255, 255, 255, 0.1); }
    .board .filled {
      -webkit-transition: background-color 110ms linear;
      -ms-transition: background-color 110ms linear;
      transition: background-color 110ms linear; }
    .board .filled.winner {
      animation: win infinite alternate ease-in-out .4s; }
  .board-slot {
    background-color: #34495e;
    border: 1px solid rgba(255, 255, 255, 0.1);
    cursor: pointer;
    height: 166.66667px;
    width: 166.66667px; }
    .board-slot:hover:not(.filled) {
      background-color: #3e5368;
      -webkit-transition: background-color 150ms linear;
      -ms-transition: background-color 150ms linear;
      transition: background-color 150ms linear; } }

@keyframes win {
  0% {
    background-size: 40%; }
  100% {
    background-size: 42%; } }
